<template>
  <div class="order">
    <div class="bg"></div>
    <div class="order-top">
      <van-nav-bar title="我的订单" left-text="返回" left-arrow @click-left="goback" />
    </div>
    <div class="orderbox">
      <div class="order-body">
        <van-tabs v-model="activeindex" @change="toggleStatus">
          <van-tab :title="item" v-for="(item,index) in titlelist" :key="index">
            <div v-if="allorderlist.length>0">
              <div class="bodybox" v-for="(it,ind,ke) in allorderlist" :key="ke">
                <div class="itembox">
                  <div class="itembox-top">
                    <div class="numbox">
                      <span>{{it.orderId}}</span>
                    </div>
                    <div class="iconText">
                      <span v-if="it.status == 1" @click="changeReceived(it,ind)">确认签收</span>
                      <div v-else>
                        <span>已签收</span>
                        <van-icon name="delete" size="16" @click="delorder(it.orderId,ind)" />
                      </div>
                    </div>
                  </div>
                  <div class="itembox-body" v-for="(v,i) in allorderlist[ind].products" :key="i">
                    <div class="elemtdata flexbetween">
                      <div class="imgandTxt flexbetween">
                        <div class="imgbox">
                          <img :src="v.smallImg" alt />
                        </div>
                        <div class="Txtbox">
                          <strong>{{v.name}}</strong>
                          <p>{{v.enname}}</p>
                          <p>{{v.rule}}</p>
                        </div>
                      </div>
                      <div class="priceandNum">
                        <div class="price">
                          <strong>￥{{v.price}}</strong>
                        </div>
                        <div class="num">
                          <span>X{{v.count}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="itembox-foot flexbetween">
                    <div class="timebox">
                      <span>{{it.date | formatDate('yyyy-MM-dd hh:mm:ss')}}</span>
                    </div>
                    <div class="allprice">
                      <span>
                        共计
                        <strong>{{it.count}}</strong>件商品
                      </span>
                      <span>合计</span>
                      <strong>￥{{it.total}}</strong>
                    </div>
                  </div>
                  <div class="anotheronebox">
                    <p @click="anotherone(ind)">再来一单</p>
                  </div>
                </div>
              </div>
            </div>
            <!-- 空显示 -->
            <div v-else class="konbox">
              <van-empty description="空空如也">
                <van-button
                  round
                  type="danger"
                  class="bottom-button"
                  color="#3a63f1"
                  @click="gohome"
                >去加购吧!</van-button>
              </van-empty>
            </div>
          </van-tab>
        </van-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/order.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      activeindex: 0,
      allorderlist: [],
      titlelist: ["全部", "待签收", "已签收"],
    };
  },
  created() {
    this.getorderData(0);
  },
  methods: {
    // 返回上一步
    goback() {
      this.$router.push({ name: "My" });
    },
    //获取数据
    getorderData(num) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findOrder",
        params: {
          appkey: this.appkey,
          tokenString,
          status: num,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 70000) {
            let nowallorderlist = {};
            this.allorderlist = [];
            result.data.result.map((v) => {
              if (!nowallorderlist[v.oid]) {
                nowallorderlist[v.oid] = {
                  orderId: v.oid,
                  date: v.createdAt,
                  count: v.count,
                  total: v.count * v.price,
                  products: [v],
                  status: v.status,
                };
              } else {
                nowallorderlist[v.oid].count += v.count;
                nowallorderlist[v.oid].total += v.count * v.price;
                nowallorderlist[v.oid].products.push(v);
              }
            });
            for (let key in nowallorderlist) {
              this.allorderlist.push(nowallorderlist[key]);
            }
            // 降序
            this.allorderlist.sort((a, b) => {
              return new Date(b.date).getTime() - new Date(a.date).getTime();
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //修改待签收
    changeReceived(item, index) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid: item.orderId,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/receive",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 80000) {
            this.$toast(result.data.msg);
            if (this.activeindex == 1) {
              this.allorderlist.splice(index, 1);
            } else {
              item.status = 2;
            }

            // this.getorderData(0);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //删除订单
    delorder(orderId, index) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push("/login");
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        oid: orderId,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/removeOrder",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 90000) {
            this.$toast(result.data.msg);
            this.allorderlist.splice(index, 1);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //切换
    toggleStatus(name) {
      this.getorderData(name);
    },
    // 跳转home界面
    gohome() {
      this.$router.push("/main");
    },
    //再来一单
    anotherone(index) {
      let products = this.allorderlist[index].products[0];

      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: products.pid,
        count: products.count,
        rule: products.rule,
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "post",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 3000) {
            //立即购买
            this.$router.push({
              name: "Pay",
              query: { sids: result.data.sid },
            });
          }
        })
        .catch((err) => {});
    },
  },
};
</script>

<style lang="less" scoped>
</style>